import React from 'react'
import { Cards } from './Cards';

export const Work = () => {

    return (
        <div className="work" id="work_mark">
            <div className="work__container">
                <div className="work__header">
                    <h1>Check Out Some of My Works.</h1>
                </div>
                <div className="work__cards">
                    <Cards />
                </div>
            </div>
        </div>
    )
}
