import React from 'react';
import { scrollTo } from '../helpers/scrollTo';

export const Header = () => {

    const img = '/assets/images/header-background.jpg';
    // const img = '/assets/images/portada.jpg';

    return (
        <header className="header__nav" style={{ backgroundImage: `url(${img})` }} id="home_mark">
            <div className="header__nav_manu">
                <div className="header__logo mouse">
                    <h3 className="">RVR</h3>
                </div>
                <nav className="navbar">
                    <ul className="navbar__menu">
                        {/* <a href="#home_mark"> */}
                        <li
                            className="mouse"
                            onClick={() => { scrollTo('home_mark') }}>Home</li>
                        {/* </a> */}
                        {/* <a href="#resume_mark"> */}
                        <li
                            className="mouse"
                            onClick={() => { scrollTo('resume_mark') }}>Resume</li>
                        {/* </a> */}
                        {/* <a href="#work_mark"> */}
                        <li className="mouse"
                            onClick={() => { scrollTo('work_mark') }}>Works</li>
                        {/* </a> */}
                        {/* <a href="#contact_mark"> */}
                        <li className="mouse"
                            onClick={() => { scrollTo('contact_mark') }}>Contact</li>
                        {/* </a> */}
                    </ul>
                </nav>
            </div>
            <div className="header__presentation">
                <div className="header__banner">
                    <h1>I'm Roberto</h1>
                    <h3 className="header__banner_line header__baner_line_anim">Software Developer</h3>
                </div>
            </div>
        </header>
    )
}
